* {
  font-family: "Raleway", sans-serif;
}

.containers {
  position: bottom;
  width: 80%;
  height: 150%;
  border: 1px solid #8dd8d0;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: auto;
}

.title {
  position: relative;
  background-color: #8dd8d0;
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.6em;
  border-radius: 5px;
}

.question {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 5px;
}

.questionBox {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 2px;
  padding: 5px;
  box-sizing: border-box;
  background-color: rgb(250, 250, 250);
}

.answerBtn {
  position: relative;
  padding: 5px;
  background-color: #80abbc;
  display: inline-block;
  margin: 2px;
  outline: none;
  border: none;
  font-size: 1em;
  color: rgb(255, 255, 255);
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
}

.score-board {
  position: relative;
  height: 700px;
}

.score {
  position: absolute;
  width: 1024px;
  font-size: 3em;
  top: 250px;
  text-align: center;
}

.playBtn {
  position: absolute;
  font-size: 1.4em;
  padding: 10px;
  top: 350px;
  background-color: #80abbc;
  color: rgb(255, 255, 255);
  width: 150px;
  left: 0px;
  right: 0px;
  margin: auto;
  outline: none;
  cursor: pointer;
}

:root {
  --off-white: #fafafa;
}
body {
  width: 100%;
}
.nav {
  background-color: #fafafa;
  background-color: var(--off-white);
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 50px;
  width: 389px;
  float: left;
  margin: 1%;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 100%;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  float: right;
  color: #31a196;
}

.nav-item {
  display: inline;
  margin-left: 2rem;

  color: #31a196;
  font-size: 25px;
}

.nav-item:hover {
  color: black;
  cursor: pointer;
}
.nav-item2 {
  display: inline;
  margin-left: 2rem;

  color: #31a196;
  font-size: 25px;
}
.nav-item2:hover {
  color: black;
  cursor: pointer;
}

.Home-section {
  height: 100vh;
  background-image: url(/updated-UX/static/media/Earthlybild.6cb72d40.png);
  background-position-x: center;
  background-size: 100%;
  background-repeat: no-repeat;
  padding-top: 80px;
}

.Warming-section {
  height: 100vh;
  background-color: white;
}

.Warming-section img {
  width: 40%;
  margin-top: 10%;
  margin-right: 3%;
  float: right;
}

#warmingheading {
  margin-top: -88px;
  padding-top: 8%;
  margin-left: 5%;
}

#warmingheading2 {
  margin-left: 10%;
}

#warmingtext {
  margin-left: 5%;
  line-height: 1.7rem;
}

.Glacier-section {
  height: 100vh;
  background-color: #fafafa;
  background-color: var(--off-white);
}

.Glacier-section img {
  width: 40%;
  margin-top: 10%;
  margin-left: 0%;
  float: left;
}
.GlacierTextSection {
  margin-top: 5%;
  width: 50%;
  float: right;
  margin-right: 5%;
}

#glacierHeading {
  padding-top: 10%;
}

#glacierText {
  line-height: 1.5rem;
  margin-bottom: 5%;
}

#glacierHeading2 {
  float: right;
  margin-right: 15%;
}

.CO2-section {
  height: 100vh;
  background-color: white;
}
.CO2-section img {
  width: 30%;
  float: right;
  padding-top: 10%;
  margin-right: 10%;
}

#CO2heading {
  padding-top: 8%;
  margin-left: 5%;
}

#CO2text {
  width: 40%;
  margin-top: 0%;
  margin-left: 5%;
  line-height: 1.5rem;
}

.Quiz-section2 {
  background-color: #fafafa;
  background-color: var(--off-white);
  padding-top: 10vh;
  padding-bottom: 10vh;
}
.quizheading {
  text-align: center;
  font-size: 4rem;
}
.startbutton {
  margin-left: 50%;
  position: center;
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #31a196;
  padding: 20px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

.startbutton:hover {
  background: #808080;
  letter-spacing: 1px;
  box-shadow: 5px 40px -10px rgba(0, 0, 0, 0.57);
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  cursor: pointer;
}

.ItalicText {
  font-style: italic;
}
h2,
h3 {
  font-family: "Raleway";
}

p {
  font-family: verdana;
  font-size: 0.75rem;
}
li {
  font-family: verdana;
}

