/* The Close button */
.closebutton {
  display: block;
  box-sizing: border-box;
  width: 35px;
  height: 35px;
  border-width: 3px;
  border-style: solid;
  border-color: black;
  border-radius: 100%;
  background: -webkit-linear-gradient(
      -45deg,
      transparent 0%,
      transparent 46%,
      white 46%,
      white 56%,
      transparent 56%,
      transparent 100%
    ),
    -webkit-linear-gradient(45deg, transparent 0%, transparent 46%, white 46%, white
          56%, transparent 56%, transparent 100%);
  background-color: black;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  position: absolute;
  margin-top: 30px;
}
.closebutton:hover {
  border-color: #31a196;
  background-color: #31a196;
  cursor: pointer;
}

